form .wrapper{
  top: 50%;
  left: 50%;
  position: absolute;
  -webkit-transform: translate(-50%,-50%);
          transform: translate(-50%,-50%);
  width: 400px;
  height: 350px;
  box-shadow: 5px 5px 30px rgba(0,0,0,0.2);
  background: #fff;
  border-radius: 10px;
  
}
form .wrapper .title{
  background: #333;
  color: #fff;
  line-height: 65px;
  text-align: center;
  font-size: 25px;
  font-weight: 500;
  border-radius: 10px 10px 0 0;
}
form .wrapper .box{
  padding: 20px 40px;
  background: #fff;
  border-radius: 10px;
}
.dropdown{
  padding: 20px;
}
.translate{
  padding: 20px;
}
button{
  outline: none;
  margin:0 90px;
  border: 0;
  font-size: 15px;
  font-family: 'Do Hyeon', sans-serif;
  line-height: 1;
  padding: 16px 30px;
  border-radius: 30px;
  background: #333;
  color: #ffffff;
  cursor: pointer;
  transition: all 0.3s linear;
  left: 150px;
}
