*, *::before, *::after {
  box-sizing: border-box;
}
form .wrapper .box .custom-field-one{
  font-size: 14px;
  border: 2px solid #ddd;
  transition: border-color 0.3s ease;
  
  
}
form .wrapper .box .custom-field-one input {
  border: none;
  -webkit-appearance: none;
  appearance: none;
  background: #f2f2f2;
  padding: var(--field-padding);
  border-radius: 3px;
  width: 250px;
  height: 25px;
  outline: none;
  font-size: 14px;
  border-color:#fff;
  transition: border-color 0.3 ease;
}

form .wrapper .box .custom-field-one .placeholder{
  position: absolute;
  left: 42px;
  font-size: 15px;
  top: 26%;
  color: #aaa;
  font-family: 'Do Hyeon', sans-serif;
  transition: 
          top 0.3s ease,
          font-size 0.3 ease,
          color 0.3 ease;
}

form .wrapper .box .custom-field-one input:valid + .placeholder,
form .wrapper .box .custom-field-one input:focus + .placeholder{
  top: 20%;
  font-size: 10px;
  color: #000;
  border-color: #000;
}

form .wrapper .box .custom-field-two .comments{
  left: 50px;
  font-size: 17px;
  font-family: 'Do Hyeon', sans-serif;
}
form .wrapper .box .custom-field-two textarea{
  background: #f2f2f2;
  border-radius: 3px;
}


form .wrapper .box .custom-select select{
  box-shadow: 0 10px 25px rgba(0,0,0,0.2);
  font-size: 15px;
  padding: 4px 20px;
  background: #333;
  color: #fff;
  font-style: bold;
  border-radius: 10px;
  left: 40px;
  cursor: pointer;
  
}
form .wrapper .box .custom-select label{
  text-align: center;
  font-size: 17px;
  font-family: 'Do Hyeon', sans-serif;
}

.image-lt{
  position: absolute;
  right: 20px;
}
.image-rt{
  right: 20%;
}








.video-item{
    display: flex !important;
    align-items: center !important;
    cursor: pointer;
}

.video-item.item img{
    max-width: 180px;
    border-radius: 10px;
}
.ui-contain{
    background-image: linear-gradient(#2f3640,#bebebe);
}

.ui_seg{
    padding: 10px;
    color: #fff;
    
}
.ui_seg .ui_form{
    padding: 54px;
    box-shadow: 5px 5px 30px rgba(0,0,0,0.2);
    border-radius: 20px;
    background-color: #fff;
    
    
}

.ui_seg .ui_form .search-box{
    position: absolute;
    top:40px;
    left: 22%;
    height: 50px;
    width: 60%;
    background-color: #2f3640;
    padding: 10px;
    border-radius: 40px;

}
.ui_seg .ui_form h3{
    position: absolute;
    color: #2f3640;
    font-family: 'Righteous', cursive;
    font-size: 30px;
    top: 40px;
    left: 20px;
}
.ui_seg .ui_form .search-box .search-txt{
    border: none;
    background: none;
    outline: none;
    left: 40px;
    padding: 0;
    color: white;
    font-size: 16px;
    font-family: 'Righteous', cursive;
    transition: 0.4s;
    line-height: 30px;
    width: 100%;
}

.ui_seg .ui_form .icon-lt{
    position: absolute;
    top: 90px;
    left: 50px;
    font-size: 18px;
}
.ui_seg .ui_form .icon-rt{
    position: absolute;
    top: 90px;
    right: 150px;
}

.ui_seg .ui_form a{
    color: #2f3640;
}
